"use client";

import { useEffect, useRef, useState } from "react";
import { Card, CardHeader, CardTitle, CardContent } from "@/components/ui/card";
import { Button } from "@/components/ui/button";
import QRCodeStyling from "qr-code-styling";
import { observer } from "mobx-react-lite";
import { useStore } from "@/stores/StoreProvider";

const ScannerComponent = observer(() => {
  const { shopStore } = useStore();
  const shopId = shopStore.shop?.id;

  const qrRef = useRef(null);
  const [qrCode, setQrCode] = useState(null);

  useEffect(() => {
    if (!shopId) return;

    const url = `${process.env.NEXT_PUBLIC_APP_URL}/claim?shopId=${shopId}`;

    const qr = new QRCodeStyling({
      width: 280,
      height: 280,
      type: "png",
      data: url,
      dotsOptions: {
        color: "#1a73e8",
        type: "rounded"
      },
      cornersSquareOptions: {
        type: "extra-rounded",
        color: "#1a73e8"
      },
      backgroundOptions: {
        color: "#ffffff"
      },
      image: "/logo.png", // <-- Add your logo here
      imageOptions: {
        crossOrigin: "anonymous",
        margin: 4,
      },
    });

    setQrCode(qr);
  }, [shopId]);

  useEffect(() => {
    if (qrCode && qrRef.current) {
      qrRef.current.innerHTML = ""; // clear old
      qrCode.append(qrRef.current);
    }
  }, [qrCode]);

  const handleDownload = () => {
    qrCode.download({ name: `shop-qr-${shopId}`, extension: "png" });
  };

  return (
    <Card className="w-full max-w-sm shadow-xl border rounded-2xl backdrop-blur bg-white/80">

      <CardHeader className="text-center pb-2">
        <CardTitle className="text-2xl font-bold tracking-tight">
          Shop QR Code
        </CardTitle>
        <p className="text-sm text-muted-foreground">
          Display this QR at your counter for customers to scan.
        </p>
      </CardHeader>

      <CardContent className="flex flex-col items-center gap-6 pt-2">

        {/* ‚≠ê Beautiful QR */}
        <div className="p-4 bg-white rounded-xl border shadow-inner">
          <div ref={qrRef} />
        </div>

        <Button
          onClick={handleDownload}
          className="w-full text-base py-2 font-medium"
        >
          Download Styled QR
        </Button>

        <p className="text-center text-xs text-muted-foreground mt-2">
          Customers scan this to access your loyalty page.
        </p>
      </CardContent>

    </Card>
  );
});

export default ScannerComponent;
